import { fromJS } from 'immutable';

// action type 用來分辨現在的Action是哪個Action
export const FORM_STORED = 'App/QuestionPage/FORM_STORED';
export const QUESTION_SUBMMITED = 'App/QuestionPage/QUESTION_SUBMMITED';

export const formStored = () => ({
  type: FORM_STORED,
});

export const questionSubmitted = (rowId) => ({
  type: QUESTION_SUBMMITED,
  rowId,
});

const initialState = fromJS({
  rowId: null,
  formIsStored: false,
});

export default function QuestionReducer(state = initialState, action) {
  switch (action.type) {
    case FORM_STORED:
      return state.set('formIsStored', true);
    case QUESTION_SUBMMITED:
      return state.set('rowId', action.rowId);
    default:
      return state;
  }
}
